@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700;900&display=swap);
.main-navigation {
  overflow: hidden;
  left: 0;
  top: 0;
  width: 100%;
  height: 5rem;
  background: #fff;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.05), 0 6px 20px 0 rgba(0, 0, 0, 0);
}

.main-navigation-logo h3 {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 1.5rem;
  color: #000;
}

.main-navigation-item {
  margin-left: 1.5rem;
}

.main-navigation-item ul {
  font-family: "Roboto", sans-serif;
  display: flex;
  list-style: none;
  font-size: 1.2rem;
  font-weight: 500;

  padding: 0;
  margin: 0;
  align-items: center;
}

.main-navigation-item li {
  margin: 0 1rem;
}

.main-navigation-item a,
.main-navigation-item button {
  text-decoration: none;
  color: #000;
  padding: 0.25rem 0.5rem;
  border: none;
  font: inherit;
  background: transparent;
  cursor: pointer;
  vertical-align: middle;
  margin: 0;
}

footer {
  margin-top: 50vh;
  background: #fafbfc;
  padding: 2rem;
  text-align: center;
  color: #888;
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

footer pre {
  color: #888;
}

.disclaimer {
  font-size: 0.8rem;
  text-align: justify;
}

.cart-items {
  background-color: #fafbfc;
  padding: 1rem;
}

.product-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
  margin: 1rem;
}

.product-container img {
  width: 15%;
  border-radius: 1rem;
}

button {
  border: none;
  padding: 1rem 2rem;
  margin-bottom: 1rem;
  border-radius: 0.5rem;
}

button:hover {
  opacity: 0.5;
}

.product-description-container button {
  width: 100%;
  background-color: #d63447;
  font-size: 1rem;
  padding: 1rem 5rem;
  color: #fff;
  font-weight: 600;
  border-radius: 10px;
  cursor: pointer;
}

.total-label-container {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 1150px) {
  .product-container {
    flex-direction: column;
  }
  .product-container img {
    width: 100%;
  }
}

.grid-container {
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
}

.grid-container-cart {
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 2fr));
}

.main-content {
  margin: 4rem 2.5rem;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  margin: 0;
}

